import Vue from 'vue'
import axios from 'axios'
import store from '@/store'

import { Toast } from 'vant'
import { ACCESS_TOKEN } from "@/store/mutation-types"
import httpConfig from "@/httpConfig";
//  axios 
// let baseURL = process.env.NODE_ENV == 'development' ? '/proxyApi' : (process.env.NODE_ENV == 'production'?httpConfig.api_url:'');
let baseURL = process.env.NODE_ENV == 'development' ? httpConfig.api_url : (process.env.NODE_ENV == 'production' ? httpConfig.api_url : '');
const service = axios.create({
  baseURL: baseURL,
  timeout: 6000 // 
})

const err = (error) => {
  if (error.response) {
    let data = error.response.data;
    const token = Vue.ls.get(ACCESS_TOKEN)
    console.log("------------", token)
    console.log("------------", error.response.status)
    switch (error.response.status) {
      case 403:
        Toast('403');
        break
      case 500:
        Toast('');
        if (token && data.message == "Token") {
          Toast('');
          // update-end- --- author:scott ------ date:20190225 ---- for:Token----
        }
        break
      case 404:
        Toast('');
        break
      case 504:
        Toast('');
        break
      case 401:
        Toast('');
        if (token) {
          store.dispatch('LogOut').then(() => {
            setTimeout(() => {
              window.location.reload()
            }, 1500)
          })
        }
        break
      default:
        Toast(',');
        break
    }
  }
  return Promise.reject(error)
};

// request interceptor()
service.interceptors.request.use(config => {
  const token = Vue.ls.get(ACCESS_TOKEN);
  if (token) {
    config.headers['token'] = token //  token 
  }
  if (config.method == 'get') {
    config.params = {
      // _t: Date.parse(new Date()) / 1000,
      ...config.params
    }
  }
  // console.log(config);
  return config
}, (error) => {
  return Promise.reject(error)
})

// response interceptor
service.interceptors.response.use((response) => {
  if(response.data.ret == 10004030){
    Toast('1');
  }else if(response.data.ret == 10001004){
    Toast('2');
  }else if(response.data.ret == 10004040){
    Toast('3');
  }
  return response.data
}, err)



export {
  service as axios
}